.sidebar{
	height: 100%;
	position: fixed;
	width: 25vw;
	background-color: black;
	padding: 0;
	margin: 0;
	color: white;
	display: flex;
	flex-direction: column;
	background-image: url('../../static/images/bgSidebar3.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;

	.cv{
		width: 100%;
		height: 18vh;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgba(0, 0, 0, 0.514);
	}
	.me{
		width: 100%;
		height: 72vh;
		background-color: transparent;
		justify-content: center;
		align-items: center;
		margin-top: 20px;
	}
	.socials{
		width: 100%;
		height: 25vh;
		background-color: transparent ;
	}
	.footer{
		width: 100%;
		height: 10vh;
		background-color: rgba(0, 0, 0, 0.514);
	}
	
}


//Responsive
@media screen and ( max-width: 992px ){
	.sidebar{
		height: 600px;
		width: 100%;
		position: relative;
		align-items: flex-start;

		.footer{
			display: none;
		}
		.cv{
			background-color: transparent;
			height: 50px ;
		}
		.socials{
			height: auto;
			align-self: flex-end;
		}
	}
}
@media screen and ( max-width: 516px ){
	.socials{
		margin-top: 30px;
		font-size: 16px;
	}
}